.BackTiles {
    position: absolute;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-evenly;
    align-content: space-evenly;
    height: 100%;
    width: 100%;
    z-index: 1;
}
.BackTile {
    background: #c9bfb1;
    border-radius: 5px;
    height: 80px;
    width: 80px;
}
